'use client';
import { useState, useEffect } from 'react';

export default function ThemeToggle() {
  const [darkMode, setDarkMode] = useState(true);

  useEffect(() => {
    const root = document.documentElement;
    if (darkMode) {
      root.style.setProperty('--bg-color', '#0a1f44');      // fond bleu fonc√©
      root.style.setProperty('--text-color', '#fff');        // texte blanc
      root.style.setProperty('--btn-bg-color', '#00bfff');   // bouton bleu ciel
      root.style.setProperty('--btn-text-color', '#fff');    // texte bouton
    } else {
      root.style.setProperty('--bg-color', '#f5f5f5');      // fond clair
      root.style.setProperty('--text-color', '#0a1f44');    // texte bleu fonc√©
      root.style.setProperty('--btn-bg-color', '#0a1f44');  // bouton bleu fonc√©
      root.style.setProperty('--btn-text-color', '#fff');   // texte bouton
    }
  }, [darkMode]);

  return (
    <button
      onClick={() => setDarkMode(!darkMode)}
      style={{
        background: 'transparent', // pas de fond
        border: 'none',            // pas de bordure
        cursor: 'pointer',
        fontSize: '1.5rem',
        color: 'var(--text-color)',
        padding: 0,                // pas de padding
      }}
    >
      {darkMode ? '‚òÄÔ∏è' : 'üåô'}
    </button>
  );
}



